/* Blog page */
.page-template.page-blog-template {
    margin-top: 0;
    margin-bottom: 60px;
    .page-title {
        text-transform: uppercase;
    } 
    .page-subtitle {
        font-size: calc(var(--body-font-size) + 2px);
        margin-bottom: 5px;
        @media (min-width: 750px) {
            font-size: calc(var(--body-font-size) + 4px); 
        }
    }
    .article-card {
        .article-card_title {
            font-size: calc(var(--body-font-size) + 2px);
        }
        .article-card_header {
            margin-bottom: 12px; 
        } 
        .article-card_excerpt {
            margin-bottom: 17px;
        }
    }
}
/* End blog post page */
/* Sidebar */
.sidebar {
    margin-bottom: 25px;
    @media (min-width: 992px) {
        margin-bottom: 0;
    }
    .sidebar_widget:not(:last-child) {
        border-bottom: 1px solid var(--body-border-color);
        margin-bottom: var(--gutter-sm);
        padding-bottom: 22px;  
    }
    .pending .sidebar_widget_title .svg-loading {
        display: block;
    }
    .sidebar_widget_title {
        font-size: calc(var(--body-font-size) + 2px);
        margin-bottom: var(--gutter-xs);
        display: flex;
        justify-content: space-between;
        align-items: center;
        .svg-loading {
            height: 20px;
            width: 32px;
            display: none;
        }
    }
    .widget-menu ul li {
        @include list-style-reset;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    } 
    .widget-featured-blogs .article {
        position: relative;
        .article_image {
            position: relative;
            overflow: hidden;
            img {
                object-fit: cover;
                @include transition-img;
            }
            &:after {
                content: "";
                @include ab(0, 0, 0, 0);
                background-color: rgba(0, 0, 0, 0.3);   
            }   
        }
        .article_title {
            @include ab(n, 15px, 15px, 15px);
            margin: 0;
            color: #ffffff;
            &:hover {
                @media (hover: hover) {
                    color: var(--body-link-hover);
                }
            }
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2);
                }
            }
        }
    } 
    .widget-tags ul {
        display: flex;
        flex-wrap: wrap; 
        li {
            @include list-style-reset;
            padding: 4px 11px;
            margin: 0 4px 12px; 
            background: #d4d4d4;
            color: #302f2f;
            border-radius: 2px;  
            a {
                color: inherit;
            }
            &.active {
                background: var(--btn2-hover-bg-cl);
                color: var(--btn2-hover-cl);
            }
        }
    }
}
/* End sidebar */
/* Article page */
.page-template.page-article-template {
    margin-top: 0;
}
.page-article-template {
    margin-top: 0;
    .article-template_header {
        margin-bottom: 6px; 
    }
    .article-template_title {
        @media (min-width: 750px) {
            margin-bottom: 3px; 
        }
    }
    .article-template_wrapper {
        flex-wrap: wrap;
        @media (min-width: 768px) {
            margin-bottom: var(--gutter-sm);
        }
    }
    .article-template_subtitle {
        color: var(--text-miscellaneous);
        flex: 0 0 100%;
        margin-bottom: 14px;
        @media (min-width: 750px) {
            margin-bottom: 0;
            flex: 1;
            font-size: calc(var(--body-font-size) + 2px); 
        }
    }
    .widget-social-icons {
        margin-bottom: 14px; 
        flex: 0 0 100%;
        @media (min-width: 750px) {
            margin-bottom: 0;
            flex: 1;
            justify-content: flex-end; 
        } 
        li {
            display: block;
            &:not(:last-child) {
                @include margin-end(8px);
            }
            a {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
            }
            svg {
                display: flex; 
            }
        }
    }
    .article-template_featured-image {
        margin-bottom: var(--gutter-sm);
    } 
    .article-template_content {
        margin-bottom: 22px;
        p {
            color: var(--body-color-2);
            strong {
                color: var(--body-color); 
            }
        }
    } 
}
/* End article page */
/* Comment */
.article-comments {
    padding-top: 19px;
    padding-bottom: var(--gutter-sm);
    border-top: 1px solid var(--body-border-color);
    .article-comments_title {
        font-size: calc(var(--body-font-size) + 2px);
        font-weight: var(--font-bold);
        @include font-heading;
    }
    ul {
        &:not(:empty) {
            margin-top: 18px;
        }
        li {
            @include list-style-reset; 
            display: flex;
            &:not(:last-child) {
                margin-bottom: var(--gutter-lg);
            }
            .comment_avatar {
                width: 40px;
                height: 40px;
                flex: 0 0 40px;
                border-radius: 50%;
                @media (min-width: 750px) {
                    width: 60px;
                    height: 60px;
                    flex: 0 0 60px;
                } 
                img {
                    object-fit: cover;
                    border-radius: 50%;
                }
            } 
            .comment_info {
                flex: 1;
                @include padding-start(8px);
                @media (min-width: 750px) {
                    @include padding-start(12px);
                }
                .comment_author {
                    font-weight: var(--font-bold);
                    @include font-heading;
                    font-size: var(--body-font-size);
                    @media (min-width: 750px) {
                        font-size: calc(var(--body-font-size) + 2px);
                    }
                }
                .comment_created-at {
                    color: var(--text-miscellaneous);
                    font-size: calc(var(--body-font-size) - 2px);
                }
                .comment_content {
                    padding-top: 8px;
                    p:last-child {
                        margin-bottom: 0;
                    } 
                } 
            }
        }
    } 
}
.comment-form {
    margin-bottom: 70px;
    padding-top: var(--gutter-xs);
    border-top: 1px solid var(--body-border-color);
    @media (min-width: 750px) {
        margin-bottom: 36px;
    }
    .comment-form_headline {
        margin-bottom: var(--gutter-sm);
        @include font-heading; 
    } 
    .comment-form_title {
        font-size: calc(var(--body-font-size) + 2px); 
    }
    .comment-form_subtitle {
        color: var(--body-color-2);
    }
    .input-group {
        margin-bottom: 28px;
    } 
    .btn { 
        width: 100%;
        @media (min-width: 750px) {
            min-width: 353px; 
            width: auto;
        }
    }
}
/* End comment */